import React from "react"
import Link from "gatsby-link"
import Layout from "../layouts/layout"
import Head from "../components/head"


const AboutMe = () => {
    return(
        <Layout>
            <Head title="About Me" />
            <h1>About Me</h1>
            <h2>Ismael Melendez</h2>
            <br/>
            <p>I am a software and web developer building technology to connect and inform people, so they are more engaged in democracy. I create applications, features, and products that help people connect to build movements that produce change. Previously, I have used social media, search engine optimization, and content creation/curation to help organizations' digital marketing. I use data to develop strategy, plan, and produce results in software development and digital marketing.</p>

            <p>I have over 3 years of experience in digital marketing and field experience in the non-profit advocacy industry as a student organizer. As a student leader developed skills in community outreach, public speaking, and event planning while developing events with over 200 students and community members, and earned local press coverage.</p>

            <p>In my work at The University of Texas Rio Grande Valley, I helped mentor over 300 high school students. Assisted in running events with over 100 participants (students and their parents) and was a staff of a department that put on over 5-grade school summer camps.</p>

           <p> Languages and Technologies: C++, Python, Javascript, SQL<br/> React, Django, Axios, Git, LAMP, Jamstack</p>

            <p>Skills: Web Development | Social Media Marketing | Writing | Editing | Nonprofit Organizations | Public Speaking | Event Planning | Lobbying | Content creator and curator </p>
            <p><Link to="/" >Take me home</Link></p>
        </Layout>   
    )
}

export default AboutMe